import React, { Component } from 'react';
import {Helmet} from "react-helmet";

class Search extends Component {
  render() {
    return (
      <div>
        Search
        <Helmet>
            <title>Search</title>
        </Helmet>
      </div>
    );
  }
}

export default Search;



